events.on('ready', function() {
    navegacao();

    function navegacao() {
        var mod1 = scorm.loadObject('trilha1', true);
        var mod2 = scorm.loadObject('trilha2', true);
        var mod3 = scorm.loadObject('trilha3', true);
        var mod4 = scorm.loadObject('trilha4', true);

        if (mod1) {
            $('.btn1').addClass('visited');
            $('.btn2').removeClass('block');
        }
        if (mod2) {
            $('.btn2').addClass('visited');
            $('.btn3').removeClass('block');
        }
        if (mod3) {
            $('.btn3').addClass('visited');
            $('.btn4').removeClass('block');
        }
        if (mod4) {
            $('.btn4').addClass('visited');
        }
    }
});